.row{z-index:0;display:grid;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);padding:var(--content-row-margin) 0;border-bottom:1px solid var(--border-tint-color)}.row.-fit{display:block;padding-bottom:unset;border:unset}.row.-flow{display:block;-moz-column-count:4;column-count:4;-moz-column-fill:balance;column-fill:balance;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--margin)}.row.-flow p:last-child{margin-bottom:unset}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 643px){.row.-flow{-moz-column-count:auto;column-count:auto}}.row:last-child{border:none}.row p .arrow{position:absolute;right:0}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 643px){.row{grid-template-columns:auto}}a.row h2{text-decoration:underline;text-decoration-color:var(--foreground-dimmed-color)}a.row:hover h2{text-decoration-color:var(--foreground-color)}