.status{display:flex;align-items:center;gap:calc(var(--font-size)*.5)}.status .dot{display:block;width:10px;height:10px;border-radius:50%}@keyframes pulse{0%{opacity:.4}50%{opacity:1}100%{opacity:.4}}.status .timer{font-size:14px;font-weight:500;line-height:1.5;letter-spacing:-0.01em}.status .timer::before{content:"Seoul, "}.status .timer::after{content:attr(meridiem)}