.tagline{grid-column:1/-1;height:calc(var(--font-size)*1.375*1.375*3)}.tagline #quotes{padding:0 .125em;color:var(--surface-color);background:var(--foreground-tint-color);border-radius:.125em}.tagline .-accent{color:var(--foreground-color) !important;background:var(--foreground-dimmed-color)}.tagline .cursor{width:0;outline:.075em solid var(--foreground-color)}.tagline .cursor.-blink{animation:blink .8s infinite}@keyframes blink{50%{opacity:0}}@media screen and (max-width: 643px){.tagline{margin-bottom:var(--content-row-margin)}}