html{display:flex;background:var(--surface-color)}body{display:flex;width:100%;color:var(--foreground-color);background:var(--surface-color)}#root{width:100%;opacity:0}main,section{display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap)}@media screen and (max-width: 1023px){main,section{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 643px){main,section{grid-template-columns:repeat(2, 1fr)}}main{max-width:var(--body);padding:0 var(--margin);margin:0 auto}main hr{grid-column:1/-1}section{padding:var(--margin) 0;color:var(--foreground-color);background:var(--surface-color)}section:first-child{padding-top:calc(var(--header)*2.5)}section:last-child{padding-bottom:calc(var(--header)*2.5)}section.content{background:var(--surface-color)}section.content::before{content:"";position:absolute;top:0;left:50%;width:100vw;height:100%;background:var(--surface-color);transform:translateX(-50%)}section.content.-rd{--foreground-color: rgb(255, 255, 255);--foreground-dimmed-color: rgba(255, 255, 255, .4);--foreground-tint-color: rgba(255, 255, 255, .2);--background-color: rgb(0, 0, 0);--surface-color: rgb(224, 64, 72);--surface-tint-color: rgb(240, 92, 102);--border-color: rgba(255, 255, 255, 1);--border-tint-color: rgba(255, 255, 255, .24)}section.content.-grn{--foreground-color: rgb(0, 0, 0);--foreground-dimmed-color: rgba(0, 0, 0, .4);--foreground-tint-color: rgba(0, 0, 0, .2);--background-color: rgb(0, 0, 0);--surface-color: rgb(80, 216, 96);--surface-tint-color: rgb(76, 200, 86);--border-color: rgb(0, 0, 0);--border-tint-color: rgba(0, 0, 0, .2)}section.content.-ppl{--foreground-color: rgb(255, 255, 255);--foreground-dimmed-color: rgba(255, 255, 255, .4);--foreground-tint-color: rgba(255, 255, 255, .2);--background-color: rgb(0, 0, 0);--surface-color: rgb(108, 96, 232);--surface-tint-color: rgb(132, 120, 248);--border-color: rgba(255, 255, 255, 1);--border-tint-color: rgba(255, 255, 255, .24)}section.content.-blu{--foreground-color: rgb(255, 255, 255);--foreground-dimmed-color: rgba(255, 255, 255, .4);--foreground-tint-color: rgba(255, 255, 255, .2);--background-color: rgb(0, 0, 0);--surface-color: rgb(40, 72, 160);--surface-tint-color: rgb(60, 102, 182);--border-color: rgba(255, 255, 255, 1);--border-tint-color: rgba(255, 255, 255, .24)}